import * as React from 'react';
import { render } from 'react-dom';
import { Editor } from 'graphql-editor';

type AppProps = {
    editorVisible: boolean;
}

const appContainer = document.getElementById('graphqleditor');
const inputElementId = document.getElementById('graphqleditor');

class App extends React.Component<
    {},
    AppProps
    > {
    state = {
        editorVisible: true
    };
    render() {
        return (
            // <input type="text" id={inputElementId.get}/>
            <div
                style={{
                    width: '100%',
                    height: '100%',
                    display: 'grid',
                    gridTemplateColumns: this.state.editorVisible ? `auto 1fr` : '1fr',
                    gridTemplateRows: '1fr'
                }}
            >
                <Editor schema={{code: ""}}/>
            </div>
        );
    }
}

render(<App />, appContainer);